dding: 10px 15px;\r\n  background-color:#1C6758;\r\n  border: 1px solid var(--button);\r\n  border-radius: 5px;\r\n  font-size: 20px;\r\n  color: #ffffff;\r\n  cursor: pointer;\r\n  transition: 0.15s ease-in-out;\r\n}\r\n\r\n.btn:hover {\r\n  background-color: #0d473c;\r\n}\r\n\r\n.btn:active {\r\n  background-color: #23806d;\r\n}\r\n\r\n/*\r\nFooter\r\n*/\r\n\r\nfooter {\r\npadding: 25px;\r\n}\r\n\r\nfooter p {\r\nfont-size: 15px;\r\ntext-align: center;\r\ncolor: #3d3d3d;\r\n}\r\n/* .btn-harmburger{\r\n  display: none;\r\n} */\r\n/*navbar*/\r\n/*about*/\r\n.about-us {\r\n  text-align: center;\r\n  padding: 20px;\r\n}\r\n.about {\r\n  margin: 32px auto auto;\r\n  text-align: left;\r\n  display: grid;\r\n  grid-row-gap: 16px;\r\n  width: fit-content;\r\n}\r\n.about-container {\r\n  justify-content: center;\r\n  column-gap: 10px;\r\n  display: flex;\r\n  width: auto;\r\n  height: auto;\r\n  padding: 10px 0;\r\n}\r\n.slide-content {\r\n  margin: 0 20px;\r\n}\r\n.card {\r\n  width: 300px;\r\n  border-radius: 25px;\r\n  background-color: #F5F3C1;\r\n}\r\n.image-content,\r\n.card-content{\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  padding: 10px 14px;\r\n  text-align: center;\r\n}\r\n.image-content {\r\n  position: relative;\r\n  row-gap: 5px;\r\n}\r\n.overlay {\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  height: 100%;\r\n  width: 100%;\r\n  background-color: #1C6758;\r\n  border-radius: 25px 25px 0 25px;\r\n}\r\n.card-image {\r\n  position: relative;\r\n  height: 100px;\r\n  width: 100px;\r\n  border-radius: 50%;\r\n}\r\n.card-image .card-img{\r\n  height: 100%;\r\n  width: 100%;\r\n  object-fit: cover;\r\n  border-radius: 50%;\r\n  border: 4px solid #2F0F5D;\r\n}\r\n.name {\r\n  font-size: 18px;\r\n  font-weight: 500;\r\n  color: #333;\r\n}\r\n.id {\r\n  font-size: 12px;\r\n  text-decoration: underline;\r\n  padding: 5px;\r\n}\r\n.description {\r\n  font-size: 12px;\r\n  color: #707070;\r\n  text-align: center;\r\n  padding: 5px;\r\n}\r\n.button {\r\n  border: none;\r\n  font-size: 16px;\r\n  color: #fff;\r\n  padding: 8px 16px;\r\n  background-color: #1C6758;\r\n  border-radius: 6px;\r\n  margin: 14px;\r\n  cursor: pointer;\r\n  transition: all 0.3 ease;\r\n}\r\n.button:hover {\r\n  background-color: #2F0F5D;\r\n}\r\n\r\n\r\n\r\n/*\r\nMain\r\n*/\r\n\r\n.main {\r\n  /* margin: 30px 30px 30px; */\r\n  margin: auto;\r\n  text-align: left;\r\n  display: grid;\r\n  grid-row-gap: 20px;\r\n  grid-column-gap: 80px;\r\n  grid-template-columns: repeat(2, 1fr);\r\n  /* gap: 100px; */\r\n  /* row-gap: 50px;\r\n  column-gap: 100px; */\r\n  width: 80%;\r\n  padding: 20px;\r\n  flex: 1;\r\n  justify-content: center;\r\n  align-content: center;\r\n}\r\n\r\n/* .card-gunung {\r\n  width: 150px;\r\n  border-radius: 25px;\r\n  background-color: red;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  padding: 10px 14px;\r\n} */\r\n\r\n.gunung-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n  background: #F5F3C1;\r\n}\r\n\r\n/* .gunung-item__header {\r\n  position: relative;\r\n} */\r\n\r\n.gunung-poster {\r\n  width: 100%;\r\n  max-height: 150px;\r\n  object-fit: fill;\r\n}\r\n\r\n.gunung-item .gunung-item__content {\r\n  padding: 16px;\r\n}\r\n\r\n.gunung-item .gunung-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n.gunung-item .gunung-item__content .gunung__title a:hover {\r\n  color:#2F0F5D;\r\n}\r\n.gunung-item .gunung-item__content p a:hover{\r\n  color: #2F0F5D;\r\n}\r\n\r\n.gunung-item .gunung-item__content h3 a {\r\n  color: #1C6758;\r\n  text-decoration: none;\r\n  display: inline-block;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n.gunung-item .gunung-item__content p a {\r\n  color: #1C6758;\r\n}\r\n.gunung-item .gunung-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4; /* number of lines to show */\r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n/* halaman detail */\r\n.detailGunung {\r\n  width: 100%;\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  padding: 10px;\r\n  box-sizing: border-box;\r\n}\r\n.gunung__name{\r\n  text-align: center;\r\n}\r\n\r\n.detailGunung .gunung__image {\r\n  width: 100%;\r\n}\r\n\r\n.detailGunung .gunung__info h4 {\r\n  padding-top:10px ;\r\n  padding-bottom: 10px;\r\n  color: #393646;\r\n}\r\n\r\n\r\n/*\r\nLike\r\n*/\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #1C6758;\r\n  \r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n  \r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/*\r\n  Loading\r\n*/\r\n\r\n.loading {\r\n  position: fixed;\r\n  z-index: 999;\r\n  height: 2em;\r\n  width: 2em;\r\n  overflow: show;\r\n  margin: auto;\r\n  top: 0;\r\n  left: 0;\r\n  bottom: 0;\r\n  right: 0;\r\n  visibility: hidden;\r\n}\r\n\r\n.loading:before {\r\n  content: '';\r\n  display: block;\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0,0,0,0.3);\r\n}\r\n\r\n.loading:not(:required) {\r\n  font: 0/0 a;\r\n  color: transparent;\r\n  text-shadow: none;\r\n  background-color: transparent;\r\n  border: 0;\r\n}\r\n\r\n.loading:not(:required):after {\r\n  content: '';\r\n  display: block;\r\n  font-size: 10px;\r\n  width: 1em;\r\n  height: 1em;\r\n  margin-top: -0.5em;\r\n  -webkit-animation: spinner 1500ms infinite linear;\r\n  -moz-animation: spinner 1500ms infinite linear;\r\n  -ms-animation: spinner 1500ms infinite linear;\r\n  -o-animation: spinner 1500ms infinite linear;\r\n  animation: spinner 1500ms infinite linear;\r\n  border-radius: 0.5em;\r\n  -webkit-box-shadow: rgba(0, 0, 0, 0.75) 1.5em 0 0 0, rgba(0, 0, 0, 0.75) 1.1em 1.1em 0 0, rgba(0, 0, 0, 0.75) 0 1.5em 0 0, rgba(0, 0, 0, 0.75) -1.1em 1.1em 0 0, rgba(0, 0, 0, 0.5) -1.5em 0 0 0, rgba(0, 0, 0, 0.5) -1.1em -1.1em 0 0, rgba(0, 0, 0, 0.75) 0 -1.5em 0 0, rgba(0, 0, 0, 0.75) 1.1em -1.1em 0 0;\r\n  box-shadow: rgba(0, 0, 0, 0.75) 1.5em 0 0 0, rgba(0, 0, 0, 0.75) 1.1em 1.1em 0 0, rgba(0, 0, 0, 0.75) 0 1.5em 0 0, rgba(0, 0, 0, 0.75) -1.1em 1.1em 0 0, rgba(0, 0, 0, 0.75) -1.5em 0 0 0, rgba(0, 0, 0, 0.75) -1.1em -1.1em 0 0, rgba(0, 0, 0, 0.75) 0 -1.5em 0 0, rgba(0, 0, 0, 0.75) 1.1em -1.1em 0 0;\r\n}"],"sourceRoot":""}]);
